* {
  box-sizing: border-box;
}

a {
  color: #fffefe;
  text-decoration: none;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #fffefe;
  background-color: #0d2c40;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen-Sans, Ubuntu, Cantarell, Helvetica Neue, sans-serif;
  line-height: 1.5;
}

img {
  max-width: 100%;
  display: block;
}

.search-timer {
  justify-content: center;
  align-items: center;
  gap: 10px;
  margin-bottom: 20px;
  display: flex;
}

.button {
  width: 100px;
  color: #fffefe;
  background-color: #173540;
  border-radius: 5px;
  padding: 12px;
  display: block;
}

.button:hover, .button:focus {
  color: #173540;
  background-color: #f2c894;
}

#datetime-picker {
  width: 300px;
  height: 25px;
  color: #217373;
  background-color: rgba(0, 0, 0, 0);
  border-radius: 5px;
  padding: 20px;
  font-size: 15px;
}

.timer {
  text-transform: uppercase;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  gap: 20px;
  display: flex;
}

.field {
  text-align: center;
  width: 100px;
  background-color: #f2c894;
  border: 1px solid #3c3d59;
  border-radius: 5px;
  padding: 15px;
}

.field > .value {
  color: #173540;
  font-size: 30px;
  font-weight: 700;
}

.field > .label {
  color: #3d3d3d;
}

